
* {
    box-sizing: border-box;
    -webkit-user-select: none;
    outline: 0;
    -webkit-tap-highlight-color: transparent;
    -webkit-tap-highlight-color: transparent;
    margin: 0px;
    padding: 0px;
}
.isActive{
    display:none
}
#app{
    width: 100%;
    padding:0px;
    margin: 0px auto;
}
.content{
    position: absolute;
    left: 0;
    right: 0;
    height: 100%;
    top:45px;
}
.g-header{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-bottom:1px solid #cccccc;
    box-shadow: 0 0 .5px #cccccc;
    line-height: 44px;
    padding:0 10px;
    cursor: pointer;
    position:fixed;
    left:0;
    right:0;
    top:0;
    background-color: #ffffff;
    z-index:9;
}
.g-header .logo{
   margin: 0 auto;
   display: block;
}
.container{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-flow: wrap;
        flex-flow: wrap;
    width:100%;
    margin-top:10px;
    padding: 0 2%;
}
.content_text{
    box-sizing: border-box;
    width:100%;
    padding:2%;
    box-shadow: 0 0 .5px #cccccc;
    border-radius: 5px;
    position:relative;
    margin-bottom: 10px;
    border: 1px solid #cccccc;
}
.content_text .text{
    -ms-flex:1;
        flex:1;
    font-size: 14px;
    letter-spacing: 1.5px;
    padding:5px;
}
.content_text .images>img,.content_text .videos>video{
    width:100%;
}
.content_text .author-timer{
    -ms-flex:1;
        flex:1;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-flow: row;
        flex-flow: row;
    height:30px;
    margin-top:2%;
}
.author-timer .author, .author-timer .timer{
    -ms-flex:1;
        flex:1;
    font-size: 12px;
    color:#666666;
}
.author-timer .author>div, .author-timer .timer>div{
    float:left;
    line-height: 30px;
}
.author-timer .timer>div{
    width:50%;
    text-align: center;
}
.author-timer .author>div:first-child{
    width:30px;
    height:30px;
}
.author-timer .author>div:last-child{
    padding-left:5px;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 80%;
}
.author-timer .author>div:first-child img{
    width:100%;
    height:100%;
    border-radius: 100%;
}
.marks{
    position:fixed;
    left: 0;
    right:0;
    top:0;
    bottom:0;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 999;
}
.scroll-hide {
    height: 100%;
    overflow: hidden;
}
.back_top{
    position:fixed;
    bottom:5%;
    width:32px;
    height:32px;
    background:url(../assets/img/top.png) no-repeat;
    background-size: cover;
    right:5%;
}



